:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;


  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}


* {
  box-sizing: border-box;
}

body {
  margin: 0;

}

#root {
  margin: 0 auto;
  max-width: 1280px;
}



/* Header */

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 65px;
  padding: 0 20px;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);

}


.header__logo {
  width: 31px;
  height: 27px;
  margin-right: 7px;
}

.header__title {
  margin: 0;
  margin-right: auto;
  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  font-size: 20.7482px;
  line-height: 24px;
  letter-spacing: -0.1em;
  color: #FFFFFF;
}

.header__text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  text-align: right;
  color: #FFFFFF;
}


/* meme */
.meme {
  padding: 0 36px;
}


.meme__form {
  margin-top: 36px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 17px;
}

.meme__input {

  border: 1px solid #D5D4D8;
  border-radius: 5px;
  text-indent: 5px;

  font-family: 'Karla';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;

}


.meme__button {
  grid-column: 1 / -1;
  background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
  border-radius: 5px;
  border: none;
  cursor: pointer;

  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;

  text-align: center;
  letter-spacing: -0.1em;

  color: #FFFFFF;
}

.gallery {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: auto;
  gap: 10px;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, .8);

}


.gallery__img {
  width: 100%;
  cursor: pointer;
}

.meme__img-container {
  max-width: 700px;
  margin: 0 auto;
}

.canvas {
  margin-top: 17px;
  width: 100%
}